import React, {Component} from 'react';
import Dashboard, {addWidget} from '../core/components/dashboard';
import {NotificationContainer, NotificationManager} from 'react-notifications';

// App components
import Header from './header.component';
import Container from './container.component';
import CustomFrame from './custom-frame.component';

import EditBar from "./edit-bar.component";
import AddWidgetDialog from "./add-dialog.component";
import JiraResolveIssuesComponent from "../panels/jira-created-resolve-issues-chart";
import JiraBugsChartComponent from "../panels/jira-bugs-chart";
import JiraSupportIssuesChartComponent from "../panels/jira-support-issues-chart";
import JenkinsAllBuildsComponent from "../panels/jenkins-all-builds/index";
import JiraBoardIssuesComponent from '../panels/jira-board-issues';
import JenkinsAppiotCoreComponent from '../panels/jenkins-appiot-core';
import JenkinsLWM2MComponent from '../panels/jenkins-appiot-lwm2m';
import CustomWidgetsFrame from "../core/components/dashboard/components/WidgetsCarouselFrame";
import configService from "../services/config.service";
import JiraSmallBoardComponent from "../panels/jira-small-board";
import JiraBoardCumulativeChartComponent from "../panels/jira-board-cumulative-chart";
import NotificationsComponent from "../core/components/notifications";

class App extends Component {
  constructor(props) {
    super(props);
    this.onSettingsSave = this.onSettingsSave.bind(this);
    this.state = {
      // Widgets that are available in the dashboard
      widgets: {
        JiraBoardProgressWidget: {
          type: JiraBoardIssuesComponent,
          title: 'Jira DDM_RHB Board'
        },
        JiraSmallBoardWidget: {
          type: JiraSmallBoardComponent,
          title: 'Jira Small DDM_RHB Board'
        },
        JiraResolveIssuesWidget: {
          type: JiraResolveIssuesComponent,
          title: 'Jira Created vs Resolved Issues Chart'
        },
        JiraBugsChartWidget: {
          type: JiraBugsChartComponent,
          title: 'Jira Created vs Resolved Bugs Chart'
        },
        JiraSupportIssuesChartWidget: {
          type: JiraSupportIssuesChartComponent,
          title: 'Jira Created vs Resolved Support Issues Chart'
        },
        JiraBoardCumulativeChartWidget: {
          type: JiraBoardCumulativeChartComponent,
          title: 'Jira Board Cumulative Chart'
        },
        JenkinsAppiotCoreWidget: {
          type: JenkinsAppiotCoreComponent,
          title: "AppIoT Core Build Monitor"
        },
        JenkinsLWM2MWidget: {
          type: JenkinsLWM2MComponent,
          title: 'AppIoT LWM2M Build Monitor'
        },
        JenkinsAllBuildsWidget: {
          type: JenkinsAllBuildsComponent,
          title: 'Jenkins All Builds Statuses'
        }
      },
      layout: {
        rows: [{
          columns: [
            {
              className: 'col-md-12 col-sm-12 col-xs-12',
              widgets: [],
            }
          ],
        }, {
          columns: [
            {
              className: 'col-md-6 col-sm-6 col-xs-6',
              widgets: [
                {key: 'JiraBoardProgressWidget', isCarousel: true},
                {key: 'JiraSmallBoardWidget', isCarousel: true}
              ]
            }
            ,
            {
              className: 'col-md-6 col-sm-6 col-xs-6',
              widgets: [
               {key: 'JenkinsAllBuildsWidget'},
                {key: 'JiraSupportIssuesChartWidget', isCarousel: true},
                {key: 'JiraBugsChartWidget', isCarousel: true}
              ]
            },
          ]
        }, {
          columns: [
            {
              className: 'col-md-6 col-sm-6 col-xs-6',
              widgets: [],
            },
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            },
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            }
          ]
        }, {
          columns: [
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            },
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            },
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            },
            {
              className: 'col-md-3 col-sm-3 col-xs-3',
              widgets: [],
            },
          ]
        }],
      },
      editMode: false,
      isModalOpen: false,
      addWidgetOptions: null,
      settings: {...configService.settings}
    };
  }

  /**
   * When a widget is removed, the layout should be set again.
   */
  onRemove = (layout) => {
    this.setState({
      layout: layout,
    });
  }

  /**
   * Adds new widgget.
   */
  onAdd = (layout, rowIndex, columnIndex, isCarousel) => {
    // Open the AddWidget dialog by seting the 'isModalOpen' to true.
    // Also preserve the details such as the layout, rowIndex, and columnIndex  in 'addWidgetOptions'.
    //  This will be used later when user picks a widget to add.

    this.setState({
      isModalOpen: true,
      addWidgetOptions: {
        layout,
        rowIndex,
        columnIndex,
        isCarousel
      },
    });
  }

  /**
   * When a widget moved, this will be called. Layout should be given back.
   */
  onMove = (layout) => {
    this.setState({
      layout: layout,
    });
  }

  /**
   * This will be called when user tries to close the modal dialog.
   */
  onRequestClose = () => {
    this.setState({
      isModalOpen: false,
    });
  }

  /**
   * When a widget moved, this will be called. Layout should be given back.
   */
  onSettingsSave = (settings) => {
    this.state = {...this.state, settings: {...settings}}
    this.setState({
      ...this.state
    });
  }

  render() {
    return (
      <Container>
        <AddWidgetDialog widgets={this.state.widgets} isModalOpen={this.state.isModalOpen}
                         onRequestClose={this.onRequestClose} onWidgetSelect={this.handleWidgetSelection}/>
        <Header editable={this.state.editMode} settings={this.state.settings} onSave={this.onSettingsSave}/>
        <EditBar onEdit={this.toggleEdit}/>
        <Dashboard
          frameComponent={CustomFrame}
          widgetsFrameComponent={CustomWidgetsFrame}
          onRemove={this.onRemove}
          layout={this.state.layout}
          widgets={this.state.widgets}
          editable={this.state.editMode}
          settings={this.state.settings}
          onAdd={this.onAdd}
          onMove={this.onMove}
          addWidgetComponentText="Add New Widget"
        />
      </Container>
    );
  }

  /**
   * Toggeles edit mode in dashboard.
   */
  toggleEdit = () => {
    this.setState({
      editMode: !this.state.editMode,
    });
  };

  /**
   * When user selects a widget from the modal dialog, this will be called.
   * By calling the 'addWidget' method, the widget could be added to the previous requested location.
   */
  handleWidgetSelection = (widgetName) => {
    const {layout, rowIndex, columnIndex, isCarousel} = this.state.addWidgetOptions;

    /**
     * 'AddWidget' method gives you the new layout.
     */
    this.setState({
      layout: addWidget(layout, rowIndex, columnIndex, widgetName, isCarousel),
    });

    // Close the dialogbox
    this.onRequestClose();
  }
}

export default App
