import './styles.css'
import React, {Component} from 'react';

import PanelLoaderComponent from "../../core/components/panel-loader";
import GetJiraBoardIssuesJob from "../../data-load-jobs/get-jira-board-issues.job";
import BoardComponent from "./board.component";

class JiraBoardIssuesComponent extends Component {

  jiraBoardIssuesJob;

  constructor() {
    super();
    this.state = {
      data: {
        items: []
      },
      isLoading: true
    };

  }

  componentDidMount() {
    const jql = "issuetype=Epic AND status=Development OR status='in progress' OR status='QUALITY ASSURANCE' OR status=done order by rank asc";
    this.jiraBoardIssuesJob = new GetJiraBoardIssuesJob('jira-board-issues', "*/7 * * * *", 1942, jql, true);
    this.jiraBoardIssuesJob
      .subscribe((data) => {
        if (data && Array.isArray(data)) {
          this.state.data.items = data.mergeByKey(data, (item) => item.key);
          this.setState({
            data: this.state.data,
            isLoading: this.state.data.items.length === 0
          });
        }
      }, (error) => {
        this.setState({
          ...this.state,
          errorMessage: error.message ? error.message : 'Loading error.'
        });

      });
  }

  componentWillUnmount() {
    this.jiraBoardIssuesJob.unsubscribe();
  }

  render() {
    return (
      <PanelLoaderComponent panel={<BoardComponent items={this.state.data.items}/>}
                            isLoading={this.state.isLoading}
                            errorMessage={this.state.errorMessage}/>
    );
  }
}

export default JiraBoardIssuesComponent;
