import React, {Component} from 'react';

import JiraCreatedResolvedIssuesChartComponent from "../../components/jira-created-resolved-issues-chart"

class JiraSupportChartComponent extends Component {

    render() {
        return <JiraCreatedResolvedIssuesChartComponent jobId={'jira-support-chart'} cronTime={"*/10 * * * *"} reportTitle={'Support Issues'} conditions={['project=DDM', 'issuetype in (Support)']}/>
    }
}

export default JiraSupportChartComponent