import React, {createElement} from 'react';
import PropTypes from 'prop-types';
import * as shortid from 'shortid';
import WidgetFrame from './WidgetFrame';
import WidgetsDefaultFrame from './WidgetsDefaultFrame'
import WidgetsCarouselFrame from "./WidgetsCarouselFrame";


/**
 * Component that renders the widget which belongs to a column.
 */
/* eslint max-len: "off" */
const Widgets = ({widgets, widgetTypes, onRemove, layout, columnIndex, rowIndex, editable, frameComponent, onMove, containerClassName, settings}) => {

  const getWidgetFrames = (widgets) => widgets.map((widget, index) => { // eslint-disable-line arrow-body-style
    return (
      <WidgetFrame
        key={shortid.generate()}
        widgetName={widget.key}
        title={widgetTypes[widget.key].title}
        onRemove={onRemove}
        layout={layout}
        columnIndex={columnIndex}
        rowIndex={rowIndex}
        widgetIndex={index}
        editable={editable}
        frameComponent={frameComponent}
        frameSettings={widgetTypes[widget.key].frameSettings}
        onMove={onMove}
      >
        {
          createElement(widgetTypes[widget.key].type, widgetTypes[widget.key].props)
        }
      </WidgetFrame>
    );
  })

  const carouselItems = widgets.filter(w => w.isCarousel);
  const defaultItems = widgets.filter(w => !w.isCarousel);

  return (
    <div className={containerClassName}>
      <WidgetsDefaultFrame
        children={getWidgetFrames(defaultItems)}
        settings={settings}
      />
      {carouselItems.length > 0 &&
      <WidgetsCarouselFrame children={getWidgetFrames(carouselItems)} settings={settings}/>}
    </div>);
};

Widgets.propTypes = {
  /**
   * CSS class name that should be provided to the widgets container.
   */
  containerClassName: PropTypes.string,
  /**
   * Widgets that should be rendered.
   */
  widgets: PropTypes.array,

  /**
   * Widgets that are available in the dashboard.
   */
  widgetTypes: PropTypes.object,

  /**
   * Function that should be called when a widget is about to be removed.
   */
  onRemove: PropTypes.func,

  /**
   * Layout of the dahsboard.
   */
  layout: PropTypes.object,

  /**
   * Index of the column these widgets should be placed.
   */
  columnIndex: PropTypes.number,

  /**
   * Index of the row these widgets should be placed.
   */
  rowIndex: PropTypes.number,

  /**
   * Indicates weatehr dashboard is in ediable mode or not.
   */
  editable: PropTypes.bool,

  /**
   * User provided widget frame that should be used instead of the default one.
   */
  frameComponent: PropTypes.func,

  /**
   * Method to call when a widget is moved.
   */
  onMove: PropTypes.func,
};

export default Widgets;
