import React, {Component} from 'react';
import {NotificationContainer, NotificationManager} from 'react-notifications';

import App from './app';
import socketsService from "../services/widget-sockets-register.service";
import loggingService from "../services/logging.service";
import configService from "../services/config.service";
import LoaderComponent from "../core/components/loader";
import NotificationsComponent from "../core/components/notifications";


class Bootstrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true
    }
    this.onReconnect = this.onReconnect.bind(this);
    this.onDisconnect = this.onDisconnect.bind(this);
  }

  onReconnect() {

    this.setState({
      isLoading: false,
    });
    window.location.reload();
  }

  onDisconnect() {
    this.setState({
      isLoading: true
    });
  }

  componentDidMount() {

    socketsService.connect(this.onReconnect, this.onDisconnect)
      .then(() => {
        this.setState({
          isLoading: false
        });
      }).catch(() => console.log("Connection error"));
  }

  render() {
    return (        <div> <button onClick={()=>NotificationManager.success('Success message', 'Title here')}>click me</button> <NotificationsComponent /></div>
      )

  }
}

export default Bootstrapper
