Array.prototype.mergeByKey = function (arr2, getKey) {
  let hash = new Map();
  [...this].concat(arr2).forEach((obj) => {
    hash.set(getKey(obj), Object.assign(hash.get(getKey(obj)) || {}, obj))
  });
  return Array.from(hash.values());
};

Array.prototype.sortByKey = function (getKey) {
  this.sort((a, b)=> {
    var nameA = getKey(a).toUpperCase();
    var nameB = getKey(b).toUpperCase();
    if (nameA < nameB) {
      return -1;
    }
    if (nameA > nameB) {
      return 1;
    }

    return 0;
  });
  return [...this]
};