import './styles.css'
import React, {Component} from 'react';
import * as shortid from "shortid";
import ColumnComponent from "./column.component";

class SmallBoardComponent extends Component {
  render() {
    const {columns} = this.props;
    return (
      <div className="jira-small-board-scroll">
        <div className="col-md-12 col-sm-12 col-xs-12 jira-small-board">
          <div className="row">
            {columns.map(column => <ColumnComponent key={shortid.generate()} header={column.name}
                                                    items={column.items}/>)}
          </div>

        </div>
      </div>
    );
  }
}

export default SmallBoardComponent;
