import './styles.css'
import React, {Component} from 'react';
import BuildComponent from "./build.component";
import PanelLoaderComponent from "../../core/components/panel-loader";
import JenkinsLastBuildsJob from "../../data-load-jobs/get-jenkins-last-builds.job";

class JenkinsBuildsListComponent extends Component {
  dataLoaderJob;

  constructor(props) {
    super(props);
    this.state = {
      builds: [],
      isLoading: true
    };
  }

  componentDidMount() {
    this.dataLoaderJob = new JenkinsLastBuildsJob(
      this.props.id,
      this.props.cronTime,
      this.props.jenkinsName,
      this.props.jobNames
    );

    this.dataLoaderJob
      .subscribe((data) => {
        if (data && Array.isArray(data)) {

          this.state.builds = data
            .mergeByKey(this.state.builds, (obj) => obj.displayName)
            .sortByKey((obj) => obj.displayName);

          this.setState({
            builds: this.state.builds,
            isLoading: this.state.builds.length === 0
          });
        }
      }, (error) => {
        this.setState({
          ...this.state,
          errorMessage: error.message ? error.message : 'Loading error.'
        });
      });
  }

  componentWillUnmount() {
    this.dataLoaderJob.unsubscribe();
  }

  renderBuilds(builds) {
    return (builds.map(build => <BuildComponent key={build.displayName} data={build}/>))
  }

  render() {
    return <PanelLoaderComponent panel={this.renderBuilds(this.state.builds)}
                                 isLoading={this.state.isLoading}
                                 errorMessage={this.state.errorMessage}/>;
  }
}

export default JenkinsBuildsListComponent
