import socketsService from '../services/widget-sockets-register.service';
import loggingService from '../services/logging.service';

class DataLoadBaseJob {

  widgetId;
  observable;
  subscription;

  constructor(widgetId, options) {
    this.widgetId = widgetId;
    this.observable = socketsService.registerWidget(this.widgetId, options);
    loggingService.log(`${this.constructor.name} registerWidget`);
  }

  subscribe(resolve, reject) {
    loggingService.log(`${this.constructor.name} subscribe`);
    this.subscription = this.observable.subscribe(data => {
      loggingService.log(`${this.constructor.name} updated`);
      if (data) {
        resolve(data);
      }
    }, error => reject(error));
  }

  unsubscribe() {
    loggingService.log(`${this.constructor.name} unsubscribe`);
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}

export default DataLoadBaseJob;
