import './styles.css'
import React, {Component} from 'react';
import * as shortid from 'shortid';
import CarouselSlide from "./carousel-slide";

class Carousel extends Component {
  constructor(props) {
    super(props);

    this.goToSlide = this.goToSlide.bind(this);
    this.goToPrevSlide = this.goToPrevSlide.bind(this);
    this.goToNextSlide = this.goToNextSlide.bind(this);
    this.update = this.update.bind(this);
    this.state = {
      activeIndex: 0,
      speed: 1000 * 60 * 2,
      timerId: undefined
    };
  }

  componentDidMount() {
    this.state = {
      activeIndex: 0,
      speed: this.props.settings.speed || 1000 * 60 * 2,
      timerId: undefined
    };
    this.update(this.props.settings);
  }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(this.props.settings) !== JSON.stringify(nextProps.settings)) // Check if it's a new user, you can also use some unique, like the ID
    {
      this.update(nextProps.settings);
    }
  }

  update(settings) {
    this.clearTimer();
    this.state.timerId =
      setInterval(() => {
        this.goToNextSlide();
      }, settings.speed * 1000);

    this.setState({
      ...this.state,
      speed: settings.speed
    });
  }

  clearTimer() {
    if (this.state.timerId) {
      clearInterval(this.state.timerId);
    }
  }

  componentWillUnmount() {
    this.clearTimer();
  }

  goToSlide(index) {
    this.setState({
      activeIndex: index
    });
  }

  goToPrevSlide(e) {
    e.preventDefault();

    let index = this.state.activeIndex;
    let {slides} = this.props;
    let slidesLength = slides.length;

    if (index < 1) {
      index = slidesLength;
    }

    --index;

    this.setState({
      activeIndex: index
    });
  }

  goToNextSlide(e) {
    if (e) {
      e.preventDefault();
    }
    let index = this.state.activeIndex;
    let {slides} = this.props;
    let slidesLength = slides.length - 1;

    if (index === slidesLength) {
      index = -1;
    }

    ++index;

    this.setState({
      activeIndex: index
    });
  }

  render() {
    return (
      <div className="carousel">
        <ul className="carousel__slides">
          {this.props.slides.map((slide, index) =>
            <CarouselSlide
              key={shortid.generate()}
              index={index}
              activeIndex={this.state.activeIndex}
              slide={slide}
            />
          )}
        </ul>
      </div>
    );
  }
}

export default Carousel;
