import './styles.css'

import React, {Component} from 'react';
import moment from "moment";

import JiraChartIssuesConverter from "./jira-chart-issues.converter";
import GetJiraSearchIssuesJob from "../../data-load-jobs/get-jira-search-issues.job";
import ChartComponent from "../../core/components/chart/chart.component"
import PanelLoaderComponent from "../../core/components/panel-loader"
import logingService from '../../services/logging.service'

class JiraCreatedResolvedIssuesChartComponent extends Component {
  jiraSearchIssuesJob;
  reportTitle;

  constructor(props) {
    super(props);
    this.state = {}
  }


  componentDidMount() {
    this.reportTitle = this.props.reportTitle;

    this.jiraChartIssuesConverter = new JiraChartIssuesConverter();
    this.currentDate = moment.utc(new Date());
    this.prevMonthDate = this.currentDate.clone().subtract(1, 'months');

    var endDateFormatted = `'${this.currentDate.format("YYYY-MM-DD")} 23:59'`;
    var startDateFormatted = `'${this.prevMonthDate.format("YYYY-MM-DD")}'`;

    const conditions = this.props.conditions.join(' AND ');
    const jobId = this.props.jobId;
    const cronTime = this.props.cronTime;

    const conditionsFormatted = `${conditions} ${conditions.length ? ' AND ' : ''}`;
    const jqls = [
      `${conditionsFormatted} created >= ${startDateFormatted} AND created <= ${endDateFormatted} ORDER BY Rank ASC`
      , `${conditionsFormatted} resolutiondate >= ${startDateFormatted} AND resolutiondate <= ${endDateFormatted} ORDER BY Rank ASC`
    ];
    this.jiraSearchIssuesJob = new GetJiraSearchIssuesJob(jobId, cronTime, jqls);
    this.jiraSearchIssuesJob
      .subscribe(responses => {
        if (responses && Array.isArray(responses) && responses.length > 1) {
          let data = {
            created: responses[0],
            resolved: responses[1]
          }
          this.state = {
            ...this.state,
            data: this.getChartData(data)
          };
          this.setState({
            ...this.state
          });
        }
      }, (error) => {
        this.setState({
          ...this.state,
          errorMessage: error.message ? error.message : 'Loading error.'
        })
      });

  }

  componentWillUnmount() {
    logingService.log("JiraCreatedResolvedIssuesChartComponent componentWillUnmount");
    this.jiraSearchIssuesJob.unsubscribe();
  }

  getChartData(data) {
    var created = this.jiraChartIssuesConverter.groupIssuesByDateRange(data.created, this.prevMonthDate, this.currentDate, 'created');
    var resolved = this.jiraChartIssuesConverter.groupIssuesByDateRange(data.resolved, this.prevMonthDate, this.currentDate, 'resolutiondate');
    return {
      title: `Created vs Resolved ${this.reportTitle} Report`,
      labels: Object.keys(created),
      datasets: [
        {
          label: "Created",
          legend: "Created: " + data.created.length,
          legendClass: "red",
          fillColor: 'rgba(255,0,0,0.2)',
          strokeColor: 'rgba(255,0,0,1)',
          pointColor: 'rgba(255,0,0,1)',
          pointStrokeColor: 'red',
          pointHighlightFill: 'red',
          pointHighlightStroke: 'rgba(220,220,220,1)',
          data: Object.values(created)
        },
        {
          label: "Resolved",
          legend: "Resolved: " + data.resolved.length,
          legendClass: "green",
          fillColor: 'rgba(0,120,0,0.2)',
          strokeColor: 'rgba(0,120,0,1)',
          pointColor: 'rgba(0,120,0,1)',
          pointStrokeColor: '#fff',
          pointHighlightFill: '#fff',
          pointHighlightStroke: 'rgba(151,187,205,1)',
          data: Object.values(resolved)
        }
      ]
    };

  }

  render() {
    return <PanelLoaderComponent panel={<ChartComponent chartData={this.state.data}/>}
                                 isLoading={this.state.data === undefined}
                                 errorMessage={this.state.errorMessage}/>
  }
}

export default JiraCreatedResolvedIssuesChartComponent