import React, {Component} from 'react';
import PropTypes from "prop-types";

import Carousel from "../../carousel";

/**
 * Default frame that will be used with the widgets.
 */
class WidgetsCarouselFrame extends Component {

  render() {
    return <Carousel slides={this.props.children} settings={this.props.settings ? this.props.settings.carousel : {}}/>
  }
}

WidgetsCarouselFrame.propTypes = {
  children: PropTypes.node,
  settings: PropTypes.object
};

export default WidgetsCarouselFrame;