import React, {Component} from 'react';

import JenkinsBuildsComponent from "../../components/jenkins-builds";

class JenkinsAllBuildsComponent extends Component {
  render() {
    const instances = [
      {
        jobs: ['bootstrap-server-build-test-merge', 'bssi-build', 'bssi-test', 'lwm2m-gateway-build-test-merge', 'lwm2m-gateway-test-image', 'pvmg-build-test-merge'],
        id: 'jenkins-jl',
        cronTime: "*/30 * * * * *",
        jenkinsName: 'jl'
      },
      {
        jobs: ['APPIoT_Autotest_API', 'AppIoT_AutoTest_Frontend', 'AppIoT_Nightly_Deploy_TestNG_V2', 'AppIoT_Server_Full_Unittest'],
        id: 'jenkins-jc',
        cronTime: "*/45 * * * * *",
        jenkinsName: 'jc'
      }];
    return (<JenkinsBuildsComponent instances={instances} />)
  }
}

export default JenkinsAllBuildsComponent
