import React, {Component} from 'react';

import JiraCreatedResolvedIssuesChartComponent from "../../components/jira-created-resolved-issues-chart"

class JiraResolveIssuesComponent extends Component {

    render() {
        return <JiraCreatedResolvedIssuesChartComponent jobId={'jira-created-resolved-chart'} cronTime={"*/9 * * * *"} reportTitle={'Issues'} conditions={['project=DDM']}/>
    }
}

export default JiraResolveIssuesComponent
