import React, {Component} from 'react';

class Header extends Component {

  constructor(props) {
    super(props);

    this.state = {
      carouselSpeed: this.props.settings.carousel.speed,
      editable: this.props.editable,
      isValidSpeed: true,
      min: 30,
      max: 3600
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleValidate = this.handleValidate.bind(this);
  }

  componentDidMount() {
    this.setState({...this.state});
  }

  isValidInteger(value) {
    return Number.isInteger(+value) && value >= this.state.min && value <= this.state.max;
  }

  handleValidate(value) {
    this.setState({
      isValidSpeed: this.isValidInteger(value)
    });

    console.log(this.state.isValidSpeed);
  }

  handleSubmit(event) {
    event.preventDefault();
    const speed = this.carouselSpeedInput.value;


    if (this.props.onSave && this.isValidInteger(speed)) {
      this.props.onSave(
        {
          carousel:
            {
              speed
            }
        }
      );
    }
  }

  render() {
    const {editable} = this.props;
    return (editable &&
      <div className={"col-md-12 col-sm-12 col-xs-12"}>
        <div className={"row"}>
          <div className={"col-md-4 col-sm-4 col-xs-4"}> &nbsp;</div>
          <div className={"col-md-4 col-sm-4 col-xs-4"}>
            <div className="dashboardHeader">
              <h3>Settings</h3>
              <form onSubmit={this.handleSubmit} className="form-inline">
                <div className="form-group mb-2 form-group-label">
                  <label>Carousel speed in seconds:</label>
                  <small className="form-text">Range: from {this.state.min} to {this.state.max} sec.</small>
                </div>
                <div className="form-group mx-sm-3 mb-2">
                  <div className="col-sm-2">
                    <input ref={(input) => this.carouselSpeedInput = input}
                           onKeyUp={() => this.handleValidate(this.carouselSpeedInput.value)}
                           type="number"
                           min={this.state.min}
                           max={this.state.max}
                           className={"form-control form-control-sm " + (this.state.isValidSpeed ? "" : "error")}
                           defaultValue={this.state.carouselSpeed}
                           id="carouselSpeed" placeholder="Enter speed"/>

                  </div>

                </div>
                <button disabled={!this.state.isValidSpeed} type="submit" className="btn btn-primary btn-sm mb-2"
                        onClick={this.onSave}>Apply
                </button>
              </form>
            </div>
          </div>
          <div className={"col-md-4 col-sm-4 col-xs-4"}>&nbsp;</div>
        </div>
      </div>
    )
  };

}

export default Header;
