import React, {Component} from 'react';

import SlotsComponent from './slots.component'
import HeaderComponent from './header.component'
import percentCalculatorService from '../../services/percent-calculator.service'
import ProgressBarComponent from "./progresbar.component";


class BuildComponent extends Component {
  render() {
    var data = this.props.data;
    var percent = getPercent(data.startTimestamp, data.estimatedDuration);

    return (
      <div className={`basic project widget ${getBuildStatus(data.result)}`}>
        <div className={"text-index"}>
          <HeaderComponent displayName={data.displayName}
                           description={data.description}
                           link={data.url}/>
          <SlotsComponent id={data.id} lastExecutionTime={data.lastExecutionTime}/>
        </div>
        {!data.result && Number.isInteger(Math.round(percent)) ?
          <ProgressBarComponent percent={percent}/> : null}
      </div>
    );
  }
}

export default BuildComponent;

function getBuildStatus(buildStatus) {
  //ABORTED, FAILURE, SUCCESS
  const buildStatuses = {
    'SUCCESS': 'successful',
    'FAILURE': 'failure',
    'ABORTED': 'aborted'
  };
  return buildStatuses[buildStatus] ? buildStatuses[buildStatus] : 'successful inprogress';
}


function getPercent(startTimestamp, estimatedDuration) {

  var getSecondsDiff = (t1, t2) => {
    var dif = t1.getTime() - t2.getTime();
    return Math.abs(dif / 1000);
  };

  var startDate = new Date(startTimestamp);
  var endDate = new Date(startTimestamp);
  endDate.setMilliseconds(endDate.getMilliseconds() + estimatedDuration);
  var percents = percentCalculatorService.calculate(getSecondsDiff(endDate, startDate), getSecondsDiff(startDate, new Date()));
  return percents <= 100 ? percents : 100;
}