import React, {Component} from 'react';

class ColumnItemComponent extends Component {
  render() {
    const {summary, priority} = this.props;
    return (
      <div className="card">
        <div className="card-body">
          <div className="row">
            <div className="col-md-1 col-sm-1 col-xs-1">
              <div className="align-top">
                <img className="status"
                     src={priority}/>
              </div>
            </div>
            <div className="col-md-11 col-sm-11 col-xs-11">
              <div className="align-top">
                <p className="card-text">{summary}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ColumnItemComponent;
