    var startDateFormatted = `'${this.prevMonthDate.format("YYYY-MM-DD")}'`;

    const createdDate = `(created >= ${startDateFormatted} AND created <= ${endDateFormatted})`;
    const resolutiondate = `(resolutiondate >= ${startDateFormatted} AND resolutiondate <= ${endDateFormatted})`;

    return `project=DDM AND ${getStatusesCondition(columnStatuses)} ${createdDate} OR ${resolutiondate} ORDER BY Rank ASC`;

