import './styles.css'
import React, {Component} from 'react';

import GetJiraBoardIssuesJob from "../../data-load-jobs/get-jira-board-issues.job";
import SmallBoardComponent from "./small-board.component";
import PanelLoaderComponent from "../../core/components/panel-loader";
import boardSettings from '../board-settings.const'

class JiraSmallBoardComponent extends Component {

  jiraBoardIssuesJob;
  columns;

  constructor() {
    super();
    this.state = {
      data: {
        columns: []
      },
      isLoading: true
    };
  }

  componentDidMount() {

    const getStatusesCondition = (statuses) => statuses.map(status => `status='${status}'`).join(' OR ');

    this.columns = [...boardSettings.columns.filter(c => c.name.toLowerCase() !== 'in service')];

    const conditions = this.columns
      .map(column => getStatusesCondition(column.statuses))
      .join(' OR ');


    const jql = `issuetype=Epic AND ${conditions} order by rank asc`;

    this.jiraBoardIssuesJob = new GetJiraBoardIssuesJob('jira-small-board-issues', "*/4 * * * *", 1942, jql, false, 1000);
    this.jiraBoardIssuesJob
      .subscribe((data) => {
        if (data && Array.isArray(data)) {
          this.state.data.columns = this.groupByColumns(data);

          this.setState({
            data: this.state.data,
            isLoading: this.state.data.columns.length === 0
          });
        }
      }, (error) => {
        this.setState({
          ...this.state,
          errorMessage: error.message ? error.message : 'Loading error.'
        });

      });
  }

  groupByColumns(issues) {
    const grouped = issues.reduce((group, item) => {
      this.columns.forEach(column => {
        let groupColumn = group.find(g => g.name == column.name);

        if (!groupColumn) {
          groupColumn = groupColumn || {};
          groupColumn.items = groupColumn.items || [];
          groupColumn.order = column.order;
          groupColumn.name = column.name;
          group.push(groupColumn);
        }

        groupColumn.items = column.statuses
          .map(s => s.toLowerCase())
          .includes(item.status.toLowerCase())
          ? [...groupColumn.items, item]
          : groupColumn.items || [];
      });
      return group;
    }, []);

    return grouped;
  }

  componentWillUnmount() {
    this.jiraBoardIssuesJob.unsubscribe();
  }

  render() {
    return (<PanelLoaderComponent panel={<SmallBoardComponent columns={this.state.data.columns}/>}
                                  isLoading={this.state.isLoading}
                                  errorMessage={this.state.errorMessage}/>)
  }
}

export default JiraSmallBoardComponent