import * as io from 'socket.io-client';

import configService from "./config.service";
import loggingService from "./logging.service";
import {BehaviorSubject} from "rxjs/index";
import {Observable} from "rxjs";


class WidgetSocketsRegisterService {
  socket;
  widgets;

  connect(onReconnect, onDisconnect) {
    if (!this.socket) {
      this.widgets = new Map();
      const socket = io.connect(configService.env.apiUrl, {secure: true, transports: ['websocket']});
      return new Promise((resolve, reject) => {

        socket.on('connect', () => {

          this.socket = socket;
          loggingService.log(`connect socket id ${this.socket.id}`);
          resolve(socket);

          socket.on('error', (error) => {
            loggingService.log(`socket id ${this.socket ? this.socket.id : 'none'}`, {error});
          });

          socket.on('disconnect', () => {
            if (onDisconnect) {
              loggingService.log('onDisconnect');
              onDisconnect();
            }
            this.disconnect();
          });

          socket.on('connect_failed', () => {
            reject({error: "Connection Failed"});
          });

          socket.on('reconnect', () => {
            if (onReconnect && onDisconnect) {
              onDisconnect();
              onReconnect();
            }
            loggingService.log('reconnect');
          });

          this.socket.on('on-data-updated', (response) => {
            const widget = this.widgets.get(response.widgetId);
            if (widget) {
              if (response.error) {
                loggingService.log(`Widget id: ${response.widgetId} Error: ${JSON.stringify(response.error)}`);
                return;
              }
              widget.subject.next(response.data);
            }
          });

          socket.on('ping', () => {
            loggingService.log('pong');
            socket.emit('pong');
          });
        });
      });
    }
  }

  registerWidget(widgetId, options) {
    const widget = this.widgets.get(widgetId);
    if (!widget) {
      loggingService.log(`WidgetSocketsRegisterService registerWidget widgetId ${widgetId}`);
      if (this.socket) {
        this.socket.emit("register-widget", {widgetId, ...options});
      }
      this.widgets.set(widgetId, {options, subject: new BehaviorSubject(null)});
      return this.listenEvent(widgetId);
    }

    return widget.subject.asObservable()
  }

  listenEvent(widgetId) {
    if (this.socket) {

      let widget = this.getWidget(widgetId);

      if (!widget) {
        return;
      }
      return widget.subject.asObservable();

    }
    return new Observable();
  }

  disconnect() {
    loggingService.log(`Disconnected`);
    if (this.socket) {
      if (!this.socket.disconnected) {
        this.socket.disconnect();
      }

      this.socket = null;
      this.widgets = new Map();
    }
  }

  getWidget(widgetId) {
    return this.widgets.get(widgetId);
  }
}

export default new WidgetSocketsRegisterService();
