import configService from './config.service'


export default {
  log: (message, data) => {
    if (configService.env.useLogging) {
      if (data) {
        console.log(message, data);
      } else {
        console.log(message);
      }
    }
  },
  error: (message, error) => {
    if (configService.env.useLogging) {
      console.error(`Error: ${message}`, error);
    }
  }
};
