import React, {Component} from 'react';
import * as shortid from "shortid";

import ColumnItemComponent from "./column-item.component";

class ColumnComponent extends Component {
  render() {
    const {header, items} = this.props;
    return (
      <div className="col-md-3 col-sm-3 col-xs-3">
        <div className="jira-small-board-column">
          <div className="jira-small-board-column-content">
            <div className="header">
              <h5>{header} <span className="badge badge-secondary">{items.length}</span></h5>
            </div>
            <ul className="jira-small-board-list">
              <li>
                {items.map(item => <ColumnItemComponent key={shortid.generate()} priority={item.priority} summary={item.summary}/>)}
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default ColumnComponent;
