import React from 'react';
import * as shortid from 'shortid';

import JenkinsBuildsListComponent from "../jenkins-builds-list";

const JenkinsBuildsComponent = (props) => {

  const {instances} = props;
  return (<div className={"build-monitor"}>
    <div id="widgets" className={'columns-2'}>
      {instances
        .map(({id, jobs, jenkinsName, cronTime}) =>
          <JenkinsBuildsListComponent key={shortid.generate()} id={id} cronTime={cronTime} jenkinsName={jenkinsName} jobNames={jobs}/>)
      }
    </div>
  </div>)

}

export default JenkinsBuildsComponent